"use client";

import React, { useState, useEffect } from "react";
import { ZoomIn, ZoomOut, Lightbulb } from "lucide-react";

interface WhiteboardProps {
  id: number;
  onAsk: () => void;
  onBranch: () => void;
  showAsk: boolean;
}

function Whiteboard({ id, onAsk, onBranch, showAsk }: WhiteboardProps) {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Simulate a response with placeholder text
    setAnswer(`Placeholder answer for board ${id}`);
    if (showAsk) {
      onAsk();
    }
  };

  if (showAsk) {
    // For the current board, display only the question input and Ask button.
    return (
      <div className="bg-white rounded-xl shadow-lg p-8 w-[420px] border border-gray-100">
        <h2 className="text-2xl font-bold mb-6 text-gray-800">
          What do you want to learn today?
        </h2>
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            value={question}
            onChange={(e) => setQuestion(e.target.value)}
            placeholder="Ask a Question"
            className="w-full px-4 py-3 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-gray-50 placeholder-gray-400 text-black"
          />
          <button
            type="submit"
            className="mt-4 w-full bg-gradient-to-r from-indigo-600 to-blue-500 text-white py-3 rounded-lg hover:from-indigo-700 hover:to-blue-600 transition-colors font-medium"
          >
            Ask
          </button>
        </form>
      </div>
    );
  } else {
    // For past boards, display the answer card with question and answer, plus a Branch button.
    return (
      <div className="bg-white rounded-xl shadow-lg p-8 w-[420px] min-h-[280px] border border-gray-100 flex flex-col justify-between">
        <div>
          {question && (
            <div className="mb-4">
              <span className="font-bold">Question: </span>
              <span className="text-gray-800">{question}</span>
            </div>
          )}
          <div>
            <span className="font-bold">Answer: </span>
            <span className="text-gray-600">
              {answer || "(info about the topic that the user requested)"}
            </span>
          </div>
        </div>
        <div className="mt-4">
          <button
            onClick={onBranch}
            className="w-full bg-green-500 text-white py-3 rounded-lg hover:bg-green-600 transition-colors font-medium"
          >
            Branch
          </button>
        </div>
      </div>
    );
  }
}

interface BranchBoardProps {
  id: number;
}
function BranchBoard({ id }: BranchBoardProps) {
  const [text, setText] = useState("");
  return (
    <div className="bg-white rounded-xl shadow-lg p-8 w-[420px] border border-gray-100">
      <input
        type="text"
        value={text}
        onChange={(e) => setText(e.target.value)}
        placeholder="Type your question here..."
        className="w-full px-4 py-3 border border-gray-200 rounded-lg focus:outline-none bg-gray-50 text-black"
      />
    </div>
  );
}

export default function Home() {
  const [zoom, setZoom] = useState(1);
  // Horizontal chain of boards, each board may have branch boards
  const [boards, setBoards] = useState<
    { id: number; branches?: { id: number }[] }[]
  >([{ id: 1 }]);
  const [nextId, setNextId] = useState(2);

  // Add a new horizontal board (to the right of the current board)
  const addBoard = () => {
    const newBoard = { id: nextId };
    setNextId((prev) => prev + 1);
    setBoards((prev) => [...prev, newBoard]);
  };

  // Add a branch board to the board with the given targetId
  const addBranch = (targetId: number) => {
    const newBranch = { id: nextId };
    setNextId((prev) => prev + 1);
    setBoards((prev) =>
      prev.map((b) =>
        b.id === targetId
          ? { ...b, branches: b.branches ? [...b.branches, newBranch] : [newBranch] }
          : b
      )
    );
  };

  const handleZoom = (direction: "in" | "out") => {
    setZoom((prev) => {
      const newZoom = direction === "in" ? prev + 0.1 : prev - 0.1;
      return Math.min(Math.max(newZoom, 0.2), 2);
    });
  };

  // Auto-fit: adjust zoom so the horizontal chain fits within the viewport.
  useEffect(() => {
    const boardWidth = 420;
    const gap = 50;
    const chainWidth = boards.length * boardWidth + (boards.length - 1) * gap;
    const availableWidth = window.innerWidth - 32;
    const fitZoom = availableWidth / chainWidth;
    setZoom(fitZoom < 1 ? fitZoom : 1);
  }, [boards]);

  return (
    <div className="min-h-screen bg-gray-100 relative">
      {/* Grid Background Pattern */}
      <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,')]" />

      {/* Header */}
      <header className="bg-white border-b border-gray-200 shadow-sm relative z-20">
        <div className="max-w-7xl mx-auto flex justify-between items-center p-4">
          {/* (Header content unchanged) */}
          <div className="flex items-center space-x-4">
            <button className="text-gray-600 hover:text-blue-500 transition-colors">
              Log in
            </button>
            <button className="bg-gradient-to-r from-indigo-600 to-blue-500 text-white px-6 py-2 rounded-lg hover:from-indigo-700 hover:to-blue-600 transition-colors">
              Sign up
            </button>
          </div>
        </div>
      </header>

      {/* Zoom Controls */}
      <div className="fixed bottom-8 right-8 flex flex-col gap-2 z-20">
        <button
          onClick={() => handleZoom("in")}
          className="bg-white p-2 rounded-lg shadow-lg hover:bg-gray-50 transition-colors"
          aria-label="Zoom in"
        >
          <ZoomIn className="w-6 h-6 text-gray-600" />
        </button>
        <button
          onClick={() => handleZoom("out")}
          className="bg-white p-2 rounded-lg shadow-lg hover:bg-gray-50 transition-colors"
          aria-label="Zoom out"
        >
          <ZoomOut className="w-6 h-6 text-gray-600" />
        </button>
      </div>

      {/* Main Content - Horizontal Chain */}
      <div className="overflow-auto min-h-[calc(100vh-4rem)]">
        <main
          className="max-w-7xl mx-auto p-8 flex flex-row justify-start items-start mt-12 relative gap-[50px]"
          style={{
            transform: `scale(${zoom})`,
            transformOrigin: "left top",
            transition: "transform 0.2s ease-out",
          }}
        >
          {boards.map((board, index) => (
            <div key={board.id} className="relative">
              {index < boards.length - 1 && (
                <div
                  className="absolute"
                  style={{
                    width: "50px",
                    height: "1px",
                    backgroundColor: "#3B82F6",
                    right: "-50px",
                    top: "50%",
                    transform: "translateY(-50%)",
                  }}
                />
              )}
              <div>
                <Whiteboard
                  id={board.id}
                  onAsk={() => {
                    if (index === boards.length - 1) addBoard();
                  }}
                  onBranch={() => {
                    if (index !== boards.length - 1) addBranch(board.id);
                  }}
                  showAsk={index === boards.length - 1}
                />
                {/* Render branch boards below this board */}
                {board.branches && board.branches.length > 0 && (
                  <div className="relative">
                    {/* Vertical connector: drawn from parent's bottom center */}
                    <div
                      className="absolute"
                      style={{
                        height: "50px",
                        width: "1px",
                        backgroundColor: "#3B82F6",
                        bottom: "0",
                        left: "50%",
                        transform: "translateX(-50%)",
                      }}
                    />
                    {/* The branch boards are rendered in a container with a top margin equal to the connector's height */}
                    <div className="mt-[50px] flex flex-col items-center gap-4">
                      {board.branches.map((branch) => (
                        <BranchBoard key={branch.id} id={branch.id} />
                      ))}
                    </div>
                  </div>
                )}
              </div>
            </div>
          ))}
        </main>
      </div>
    </div>
  );
}

interface BranchBoardProps {
  id: number;
}
